import { analyzePlurals, getPluralChanges } from './plurals';
import { FORMS } from './pluralforms';

export function convertSubKeys(subKeys, sourceLng, targetLng) {
  const analyzed = analyzePlurals(subKeys, sourceLng);

  // might be just an array
  if (analyzed.outBounds.length > 0 || analyzed.plurals.length < 1)
    return { convertedSubKeys: subKeys, meta: {} };
  const changes = getPluralChanges(sourceLng, targetLng);

  let convertedSubKeys = subKeys.reduce((mem, sk) => {
    if (changes.remove.indexOf(sk) > -1) return mem;
    mem.push(sk);
    return mem;
  }, []);

  changes.add.forEach((sk) => {
    if (convertedSubKeys.indexOf(sk) < 0) convertedSubKeys.push(sk);
  });

  convertedSubKeys = convertedSubKeys.sort();

  const meta = convertedSubKeys.reduce((mem, sk) => {
    if (changes.add.indexOf(sk) > -1) {
      const tag = FORMS.indexOf(sk) > -1 ? sk : undefined;
      mem[sk] = { type: 'plural', tag };
    }
    return mem;
  }, {});

  return { convertedSubKeys, meta };
}
