import { keyContainedFeatures } from '@inweso-dev/format-i18next-v4';

export function getSubKey(key) {
  let groupKey = key;
  let subKey;

  const features = keyContainedFeatures(key);
  if (features[2] || features[3]) {
    const p = key.split('_');
    const last = p.pop();
    const prelast = p.length > 1 ? p.pop() : null;

    if (features[2] && features[3]) {
      subKey = `${prelast}_${last}`;
      groupKey = p.join('_');
    } else {
      subKey = `${last}`;
      groupKey = `${p.join('_')}${prelast ? '_' + prelast : ''}`;
    }
  }

  return { groupKey, subKey };
}
