import plural from 'plurals-cldr';
import { FORMS } from './pluralforms';

export function analyzePlurals(subKeys, lng) {
  return subKeys.reduce(
    (mem, sk) => {
      if (sk === '__') return mem;
      if (isPlural(sk, lng)) mem.plurals.push(sk);
      return mem;
    },
    { outBounds: [], plurals: [] },
  );
}

export function isPlural(subKey, lng) {
  function isValidPluralSuffix(nr) {
    if (FORMS.indexOf(nr) > -1) return true;
    return false;
  }

  return isValidPluralSuffix(subKey);
}

export function getPluralChanges(sourceLng, targetLng) {
  let sRule = plural.forms(sourceLng);
  let tRule = plural.forms(targetLng);

  if (!sRule || !tRule) return { add: [], remove: [] };

  const remove = [];
  const add = [];

  tRule.forEach((form) => {
    add.push(form);
  });

  sRule.forEach((form) => {
    remove.push(form);
  });

  return {
    add,
    remove,
  };
}
