import { FORMS } from './pluralforms';

const nestingREGEX = new RegExp('(\\$t\\([^\\)]+\\))', 'g');
const interpolateREGEXP = new RegExp('({{[^}]+}})', 'g');

export const FEATURES = ['interpolation', 'nesting', 'plural', 'context'];

export function valueContainedFeatures(str, bag = [0, 0, 0, 0]) {
  if (!str) return bag;
  const hasNesting = str.match(nestingREGEX);
  const hasInterpolation = str.match(interpolateREGEXP);

  if (hasInterpolation) bag[0] += 1;
  if (hasNesting) bag[1] += 1;

  return bag;
}

export function keyContainedFeatures(str, bag = [0, 0, 0, 0]) {
  if (!str || str.lastIndexOf('_') < 1) return bag;

  function whatFeature(suffix) {
    if (!suffix || suffix.indexOf(' ') > -1) return null;
    if (FORMS.indexOf(suffix) > -1) return 2;
    return 3;
  }

  const p = str.split('_');
  const last = p.pop();
  const prelast = p.length > 1 ? p.pop() : null;

  const lastDetected = whatFeature(last);
  if (lastDetected) bag[lastDetected] += 1;
  const prelastDetected = whatFeature(prelast);
  if (prelastDetected) bag[prelastDetected] += 1;

  return bag;
}

export function fileContainedFeatures(data) {
  const bag = [0, 0, 0, 0];

  Object.keys(data).forEach((k) => {
    valueContainedFeatures(data[k], bag);
    keyContainedFeatures(k, bag);
  });

  return bag;
}
